import axios from 'axios';

// Creeaz instana Axios
const API = axios.create({
  baseURL: 'https://movieinfo-ftki.onrender.com', // URL-ul backend-ului
  withCredentials: true,
  // headers: {
  //   'Content-Type': 'application/json',
  // },
});

// Adaug un interceptor pentru a ataa token-ul JWT la fiecare request
API.interceptors.request.use(config => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
}, error => {
  return Promise.reject(error);
});

// Interceptor Response
// Actualizeaz interceptorul de response
API.interceptors.response.use(
  response => response,
  error => {
    const originalRequest = error.config;
    const isPublicEndpoint = [
      '/auth/login',
      '/auth/register',
      '/api/movies/recommendations',
      '/api/movies/search',
      '/api/movies/genres',
      /^\/api\/movies\/\d+$/
    ].some(pattern => {
      if (typeof pattern === 'string') return originalRequest.url.includes(pattern);
      return pattern.test(originalRequest.url);
    });

    if (error.response?.status === 401 && !isPublicEndpoint) {
      localStorage.removeItem('token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

// Funcie pentru obinerea detaliilor unui film
export const getMovieDetails = async (movieId) => {
  try {
      const response = await API.get(`/api/movies/${movieId}`);
      return {
          ...response.data,
          // Normalizeaz datele pentru cazurile unde backend-ul nu trimite valori
          actors: response.data.actors || [],
          reviews: response.data.reviews || [],
          averageRating: response.data.averageRating || 0
      };
  } catch (error) {
      if (error.response?.status === 404) {
          throw new Error('Filmul nu a fost gsit');
      }
      throw new Error('Eroare de server');
  }
};

// Funcie pentru nregistrarea utilizatorului
export const registerUser = async (userData) => {
  try {
    const response = await API.post('/auth/register', userData);
    return response.data;
  } catch (error) {
    throw error;
  }
};

// Funcie pentru autentificarea utilizatorului
export const loginUser = async (credentials) => {
  try {
    const response = await API.post('/auth/login', credentials);
    if (response.data.token) {
      localStorage.setItem('token', response.data.token); // Salveaz token-ul
      API.defaults.headers.common['Authorization'] = `Bearer ${response.data.token}`; // Seteaz header implicit
    }
    return response.data;
  } catch (error) {
    throw error;
  }
};
export const searchMovies = async (title) => {
  try {
    const encodedTitle = encodeURIComponent(title);
    const response = await API.get(`/api/movies/search?title=${encodedTitle}`);
    return response.data;
  } catch (error) {
    throw error;
  }
};

export default API;
