import API from './api'; // Import instana Axios

// Obine review-ul utilizatorului pentru un film specific
export const getUserReviewForMovie = async (movieId) => {
    try {
        const response = await API.get(`/reviews/user/${movieId}`);
        return response.data;
    } catch (error) {
        if (error.response?.status === 401) {
            return null; 
        }
        throw error;
    }
};

// Adaug un review nou
// services/reviewApi.js
export const addReview = async (reviewData) => {
    try {
        // Nu mai trimite userId n reviewData
        const response = await API.post('/reviews/add', {
            movieId: reviewData.movieId,
            rating: reviewData.rating,
            comment: reviewData.comment,
            reviewType: reviewData.reviewType
        });
        return response.data;
    } catch (error) {
        console.error('Error adding review:', error);
        throw error;
    }
};

// Actualizeaz un review existent
export const updateReview = async (reviewId, reviewData) => {
    try {
        const response = await API.put(`/reviews/update/${reviewId}`, reviewData);
        return response.data;
    } catch (error) {
        console.error('Error updating review:', error);
        throw error;
    }
};

// terge un review
export const deleteReview = async (reviewId) => {
    try {
        await API.delete(`/reviews/delete/${reviewId}`);
    } catch (error) {
        console.error('Error deleting review:', error);
        throw error;
    }
};